.popup__title {
    max-width: 330px;
    margin: 0 auto;
    padding:0 0 20px 0;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    color: #000;
    text-align: center;
}

@media screen and (max-width: 767px) {
    .popup__title {
        max-width: 238px;
        font-size: 18px;
        line-height: 22px;
    }
}